import {REGISTER, REGISTER_SUCCESS} from "../Redux/finalFormReducer";
import {authAPI} from "../Api/Api";
import {GetAuthThunkCreator} from "../Redux/authReduser";
import {SetLoading} from "../Redux/PreloaderReduser";


const submit = async (values) => {
    return authAPI.getLogin(values.Login, values.Password, values.rememberMe)
};

/** This is to mimic the behavior of one of the various Redux async middlewares */
const asyncSubmissionMiddleware = (store) => (next: Next) => (action: Action)=> {
    if (action && action.type === REGISTER) {
        store.dispatch(SetLoading(true))
        submit(action.payload).then(
            (data) => {
                if (data.resultCode !== 0) {
                    store.dispatch({type: REGISTER_SUCCESS, payload: data.messages})
                } else {
                    store.dispatch(GetAuthThunkCreator())
                }
                store.dispatch(SetLoading(false))
            }
        );
    }
    return next(action);
};

export default asyncSubmissionMiddleware;
