import {connect} from "react-redux";
import LoginPage from "./LoginPage";
import {Redirect} from "react-router-dom";
import React from "react";

class LoginPageContainer extends React.Component {

    render() {
        if (this.props.isLogged === true) {
            return (
                <Redirect to={"/Profile/" + this.props.isLoggedId}/>
            )
        }
        return <LoginPage {...this.props} />
    }
}

function mapStateToProps(state) {
    return {
        isLogged: state.auth.isLogged,
        isLoggedId: state.auth.id,
        isLoading: state.preloader.isLoading,
        submitError: state.finalForm.loginForm?.submitErrors
    };
}

export default connect(mapStateToProps, undefined)(LoginPageContainer)


