//Редьюсер это чистая функция которая принимает старый State и Action, если нужно этот старый State модифицирует
// по правилам иммутабельности (то есть не меняет старый State, а копирует его в новый State и уже меняет вносит
// изменения в новый State) после чего возвращает новый State или старый State в зависимости были изменения или нет
import {authAPI} from "../Api/Api";
import {SetLoading} from "./PreloaderReduser";

const SETAUTHINFODATA = "SETAUTHINFODATA"

const SETLOGGED = "SETLOGGED"

//Создаем для Редакса инициализационный Стейт для первичной инициализации стейта, т.к. он не может быть undefined
let initialState = {
    id: null,
    login: null,
    email: null,
    isLogged: false,
}

const AuthReducer = (state = initialState, action) => {
    switch (action.type) {

        case SETAUTHINFODATA: {
            return {
                ...state,
                id: action.AuthData.id,
                login: action.AuthData.login,
                email: action.AuthData.email
            }
        }

        case SETLOGGED: {
            return {...state, isLogged: action.isLogged}
        }
        default:
            return state
    }
}

export const SetAuthInfoActionCreator = (AuthData) => {
    return {
        type: SETAUTHINFODATA,
        AuthData
    }
}

export const SetLoggedActionCreator = (isLogged) => {
    return {
        type: SETLOGGED,
        isLogged
    }
}

export const GetAuthThunkCreator = () => {
    return (dispatch) => {
        dispatch(SetLoading(true))
        return authAPI.getAuth().then(data => {
            if (data.resultCode === 0) {
                dispatch(SetAuthInfoActionCreator(data.data));
                dispatch(SetLoggedActionCreator(true))
            } else {
                dispatch(SetLoggedActionCreator(false))
            }
            dispatch(SetLoading(false))
        });
}}

export const GetLogOutThunkCreator = () => {
    return (dispatch) => {
        dispatch(SetLoading(true))
        authAPI.logOut()
            .then(data => {
                if (data.resultCode === 0) {
                    dispatch(SetAuthInfoActionCreator({id: null, login: null, email: null}))
                    dispatch(SetLoggedActionCreator(false))
                }
                dispatch(SetLoading(false))
            });
    }
}

export default AuthReducer
