import React from 'react';
import classes from "./ProfileInfo.module.css"
import img from './1.png'
import Preloader from "../../common/Preloader";

const ProfileInfo = (props) => {

    if (!props.profile) {
        return null
    }
    if (props.isLoading === true) {
        return (
            <Preloader/>
        )
    }
	return (
      <div className={classes.ProfileInfo}>
        <div className={classes.avatar_img}>
          <img src={!props.profile.photos.large ? null : props.profile.photos.large} alt={!props.profile.photos.large ? props.profile.fullName + " didn't add foto" : props.profile.fullName}/>
        </div>
        <div className={classes.Name}>
            {props.profile.fullName}
        </div>
        <div className={classes.About}>
          About:&nbsp;{!props.profile.aboutMe ? "-": props.profile.aboutMe}
        </div>
        <div className={classes.lookingForAJob}>
            looking For A Job:&nbsp;{!props.profile.lookingForAJob ? "-":"YES"}
        </div>
        <div className={classes.lookingForAJobDescription}>
            Description Of Job:&nbsp;{!props.profile.lookingForAJobDescription ? "-" : props.profile.lookingForAJobDescription}
        </div>
        <div className={classes.Web}>
            Contacts:&nbsp;
            <select>
                <option>facebook:&nbsp;{!props.profile.contacts.facebook ? "-" : props.profile.contacts.facebook}</option>
                <option>github:&nbsp;{!props.profile.contacts.github ? "-" : props.profile.contacts.github}</option>
                <option>instagram:&nbsp;{!props.profile.contacts.instagram ? "-" : props.profile.contacts.instagram}</option>
                <option>mainLink:&nbsp;{!props.profile.contacts.mainLink ? "-" : props.profile.contacts.mainLink}</option>
                <option>twitter:&nbsp;{!props.profile.contacts.twitter ? "-" : props.profile.contacts.twitter}</option>
                <option>vk:&nbsp;{!props.profile.contacts.vk ? "-" : props.profile.contacts.vk}</option>
                <option>website:&nbsp;{!props.profile.contacts.website ? "-" : props.profile.contacts.website}</option>
                <option>youtube:&nbsp;{!props.profile.contacts.youtube ? "-" : props.profile.contacts.youtube}</option>
            </select>
        </div>
      </div>
		)
}
export default ProfileInfo;